<template>
    <div class='base-configuration-wrapper'>
        <router-view>
            <!-- Wizard Jump Options -->
            <template v-slot:navigation-section-select>
                <select class='section-select' v-on:change="setActiveForm">
                    <option selected disabled>Jump To Section</option>
                    <option value=0 :selected="formIndex === 0">Initial</option>
                    <option value=1 :selected="formIndex === 1">Inhabitants</option>
                    <option value=2 :selected="formIndex === 2">Greenhouse</option>
                    <option value=3 :selected="formIndex === 3">Energy</option>
                    <option value=4 :selected="formIndex === 4">Finalize</option>
                </select>
            </template>
            <template v-slot:main-wizard-input>
                <component :is="activeForm" v-if="activeForm != 'Finalize'"/>
                <section class='form-wrapper' v-if="activeForm === 'Finalize'">
                    <Presets/>
                    <Initial/>
                    <Inhabitants/>
                    <Greenhouse/>
                    <Energy/>
                </section>
            </template>
            <template v-slot:wizard-configuration-footer>
                <nav class='configuration-button-wrapper'>
                    <button class='btn-previous' @click='decrementIndex' v-if="!isFirstForm">Previous Section</button>
                    <button class='btn-next' @click="incrementIndex" v-if="!isFinalForm">Next Section</button>
                    <button class='btn-finalize' @click="finalizeConfiguration" v-if="isFinalForm">Finalize Settings</button>
                </nav>
            </template>

            <template v-slot:main-wizard-reference>
                <keep-alive>
                    <component :is="getActiveReference"/>
                </keep-alive>
                <!--<Reference/>-->
                <!--<GreenhouseDoughnut/>-->
            </template>  
            <template v-slot:footer-wizard-reference>
            </template>         
        </router-view>
    </div>
        
</template>

<script>
import axios from 'axios'
//import form components
import {Inhabitants,Greenhouse,Initial,Energy,Reference,Graphs,Presets} from '../components/configuration'
import {GreenhouseDoughnut} from '../components/graphs'
import {mapState,mapGetters,mapMutations} from 'vuex'
export default {
    components:{
        'Energy':Energy,
        'Presets':Presets,
        'Inhabitants':Inhabitants,
        'Greenhouse':Greenhouse,
        'Initial':Initial,
        'Reference':Reference,
        'GreenhouseDoughnut':GreenhouseDoughnut,
        'Graphs':Graphs
    },
    data(){
        return{
            formIndex:0,
            activeForm:"Initial",
            gameId:'',
            menuActive:false,
            stepMax:1,
            greenhouseSize:{
                'none':0,
                'greenhouse_small':490,
                'greenhouse_medium':2454,
                'greenhouse_large':5610
            },
        }
    },
    mounted:function(){
        this.activeForm = this.getActiveForm(this.formIndex)
    },
    computed:{
        ...mapGetters('wizard',['getConfiguration','getActiveForm','getFormLength','getFormattedConfiguration']),
        ...mapGetters('wizard',['getActiveReference','getActiveRefEntry']),
        ...mapGetters('dashboard',['getStepParams']),

        isFinalForm:function() {
            return (this.getFormLength-1) === this.formIndex
        },
        isFirstForm:function() {
            return this.formIndex === 0
        },
    },
    methods:{
        ...mapMutations(['SETGAMEID']),
        

        toggleMenu:function(){
            this.menuActive = !this.menuActive
        },
        setActiveForm:function(event){
            let {value:index} = event.target
            this.formIndex = parseInt(index)
            this.activeForm = this.getActiveForm(index)
        },
        decrementIndex:function(){
            let index = Math.max(0,(this.formIndex-1))      
            this.formIndex = index
        },
        incrementIndex:function(){
            
            let max = this.getFormLength-1
            this.formIndex = Math.min(max,(this.formIndex+1))       
        },
        finalizeConfiguration:async function(){
            const configParams = {game_config:this.getFormattedConfiguration}
            const localHost = "http://localhost:8000"
            const configurationRoute =localHost + "/new_game"

            try{
                const response = await axios.post(configurationRoute,configParams)
                const gameID = response.data
                this.gameId = gameID
                this.SETGAMEID(gameID)
                this.$router.push('dashboard')
            }catch(error){
                console.log(error)
            }
        },
        initialSetup: async function(){
            const localHost = "http://localhost:8000"
            const getStepToRoute = localHost + "/get_step_to"
            const getStepRoute = localHost + "/get_steps"

            const stepToParams = {step_num:1000,game_id:this.gameId}
            //const stepParams = this.getStepParams(this.gameId,this.stepMax,10)
            const stepParams = {  
                "game_id":this.gameId,
                "min_step_num": this.stepMax, 
                "n_steps": 10,
                "total_production":["atmo_co2","atmo_o2","h2o_potb","enrg_kwh"],
                "total_consumption":["atmo_o2","h2o_potb","enrg_kwh"],
                "storage_ratios":{"air_storage_1":["atmo_co2","atmo_o2","atmo_ch4","atmo_n2","atmo_h2","atmo_h2o"]},
                "parse_filters":[],
                "single_agent":1,
            }

            try{
                await axios.post(getStepToRoute,stepToParams)
                setTimeout(async ()=>{
                    const response = await axios.post(getStepRoute,stepParams)
                    this.stepBuffer(response)
                },2000)

            }catch(error){
                console.log(error)
            }
        },        
        stepBuffer: async function(response){
            //Add in termination condition check

            const localHost = "http://localhost:8000"
            const getStepRoute = localHost + "/get_steps"

            let nSteps = this.stepMax >= 100 ? 100 : 10

            let stepData = Object.entries(response.data) // This will be removed later
            this.stepMax = this.stepMax + stepData.length // This will be removed later
            //const stepParams = this.getStepParams(this.gameId,this.stepMax,nSteps)

            const stepParams = {  
                "game_id":this.gameId,
                "min_step_num": this.stepMax, 
                "n_steps": nSteps,
                "total_production":["atmo_co2","atmo_o2","h2o_potb","enrg_kwh"],
                "total_consumption":["atmo_o2","h2o_potb","enrg_kwh"],
                "storage_ratios":{"air_storage_1":["atmo_co2","atmo_o2","atmo_ch4","atmo_n2","atmo_h2","atmo_h2o"]},
                "parse_filters":[],
                "single_agent":1,
            }

            let callback = this.stepBuffer

            try{
                const response = await axios.post(getStepRoute,stepParams)
                callback(response)
            }catch(error){
                console.log(error)
            }
        },
    },
    watch:{
        getActiveForm:{
            handler:function(){
                this.activeForm = this.getActiveForm(this.formIndex)
            },
            deep:true
        },
        formIndex:{
            handler:function(){
                this.activeForm = this.getActiveForm(this.formIndex)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    .base-configuration-wrapper{
        position:relative;
        width: 1024px;
        height: 768px;
        max-height: 768px;
        overflow:hidden;
    }

    .form-wrapper{
        *:not(:last-of-type){
            margin-bottom:24px;
        }
    }

    .configuration-button-wrapper{
        display:flex;
        justify-content:flex-start;
        align-items:center;
    }

    .btn-next{
        width: 196px;
        height: 48px;
        min-height: 48px;
        margin-left: auto;
        margin-bottom: 24px;
        border-radius: 24px;
        padding: 12px 16px;
        font-size: 16px;
        font-weight: 600;
        background-color: #0099ee;
        border:none;
        color: #eee;

        &:hover{
            cursor: pointer;
        }

        &:focus{
            outline:none;
        }
    }

    .btn-previous{
        width: 196px;
        height: 48px;
        min-height: 48px;
        margin-bottom: 24px;
        border-radius: 24px;
        padding: 12px 16px;
        font-size: 16px;
        font-weight: 600;
        background-color: transparent;
        border:2px solid #ff3100;
        color: #eee;

        &:hover{
            cursor: pointer;
        }

        &:focus{
            outline:none;
        }
    }

    .btn-finalize{
        width: 196px;
        height: 48px;
        min-height: 48px;
        margin-left:auto;
        margin-bottom: 24px;
        border-radius: 24px;
        padding: 12px 16px;
        font-size: 16px;
        font-weight: 600;
        background-color: #ff3100;
        border:none;
        color: #eee;

        &:hover{
            cursor: pointer;
        }

        &:focus{
            outline:none;
        }
    }
</style>
    